$form-text-main: #fff;
$form-text-sub-a: #b9c4d4;
$form-text-sub-b: #7988a1;
$form-text-btn: #022047;
$form-text-hl: #00ceb8;
$form-bg-main: #2d3649;
$form-bg-hover: #1c212d;
$form-bg-select: rgba(0,206,184,0.2);

$error-red: #ff2b6d;

$pga-a: #003C80;
$pga-b: #F1373D;

$masters-a: #076652;
$masters-b: #1BBA9A;
$masters-c: #E5D743;


$liv-a: #CBFB7E;
$liv-b: #6AE8EC;

$font-xl: 1.5rem;
$font-l: 1rem;
$font-m: 1rem;
$font-s: 0.8rem;
$font-xs: 0.8rem;


/* Display Settings
~~~~~~~~~~~~~~~~~~~~ */
.modal-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(204, 26, 26, 0.55);
    z-index: 1000;
    width: 100vw;
}

.new-league-modal {
    position: absolute; 
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;

    min-width: 500px;
    background-color: $form-bg-main;
    border-radius: 2.4rem;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .step-back {
        height: 3.5rem;
        width: 3.5rem;
        position: absolute;
        left: $font-l;
        top: $font-l;
        padding: 0.5rem;
        border-radius: 10rem;
        transition: 0.2s;

        svg {
            width: 100%;
            height: 100%;
            fill: $form-text-sub-a;
            transition: 0.2s;
        }
    }

    .step-back.disable {
        visibility: hidden;
    }

    .step-back:hover {
        background-color: $form-bg-hover;
        transition: 0.2s;

        svg {
            fill: $form-text-main;
            transition: 0.2s;
        }
    }

    .new-league-header {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 3rem;

        h1 {
            font-size: $font-xl;
            padding-bottom: 1rem;
            font-family: poppins, sans-serif;
            font-weight: 700;
            font-style: italic;
            color: $form-text-main;
        }
        
        p {
            font-size: $font-l;
            color: $form-text-sub-b;
            padding-bottom: 0.5rem;
        }
        div.progress-container {
            background-color: $form-text-sub-b;
            width: 250px;
            height: 5px;
            border-radius: 10rem;
            overflow: hidden;
            // display: flex;
            // justify-content: flex-start;
            // align-items: center;

            .progress-bar {
                height: 5px;
                background-color: $form-text-hl;
                transition: width 0.5s;
            }
        }
    }

    .new-league-header:nth-child(3) {
        margin-bottom: 0rem;
    }

    .league-options-row {
        display: flex;
        justify-content: space-around;

        .league-option-btn {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            padding: $font-l;
            width: 240px;
            cursor: pointer;
            
            div {
                width: 200px;
                height: 200px;
                background-color: red;
                border-radius: $font-m;
                padding: 0.5rem;

                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
                margin-bottom: 0.5rem;


                p{
                    font-size: $font-l;
                    font-family: poppins, sans-serif;
                    font-weight: 700;
                    font-style: italic;

                }

                svg {
                    width: 100%;
                    flex-grow: 1;
                }
            }

            .pga-img {
                background: linear-gradient(41deg, $masters-a 30%, $masters-b 70%);

                p {
                    color: white;
                }
            }

            .liv-img {
                background: linear-gradient(319deg, $liv-a 30%, $liv-b 70%);
                p {
                    color: black;
                }
            }

            p {
                font-size: $font-s;
                line-height: 1.2;
                color: $form-text-sub-a;
                font-family: 'poppins', sans-serif;
                font-weight: 600;
                padding: 0.5rem 1rem;
            }
        }
    }

    .league-inputs-container {
        width: 400px;
        margin: 0rem $font-l $font-l $font-l;

        .league-input-item {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            margin-top: 2rem;

            .league-input-header {
                width: 100%;
                padding-bottom: 0.5rem;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-bottom: 0.5rem;

                label {
                    font-size: $font-m;
                    padding-right: 1rem;
                    color: $form-text-main;
                }
                div {
                    flex-grow: 1;
                    // border-top-style: dashed;
                    // border-width: 1px;
                    // border-color: $form-text-hl;
                    border-bottom: 1px dashed $form-text-sub-b;
                }
            }

            p.input-notes {
                padding-bottom: 0.5rem;
                font-size: $font-xs;
                color: $form-text-sub-b;
                font-family: 'poppins', sans-serif;
                font-weight: 600;
                line-height: 1.2;
            }

            input, select {
                border: 1px solid $form-text-main;
                border-radius: 10rem;
                transition: border 0.2s;
                background-color: transparent;
                font-family: poppins, sans-serif;
                font-weight: 700;
                font-size: $font-m;
                padding: 0.5rem $font-m;
                color: $form-text-main;
                margin-bottom: 0.5rem;
                cursor: pointer;
            }

            ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
                color: $form-text-sub-b
            }

            input:focus, input:focus-visible, select:focus, select:focus-visible  {
                border: 1px solid $form-text-hl;
                transition: border 0.2s;
                outline: none;
            }
            
            p[id*="input-error"] {
                font-size: $font-s;
                color: $error-red;
                visibility: hidden;
            }

            p[id*="input-error"].invalid {
                visibility: visible;
            }

            .new-league-draft-type {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                padding: 0.5rem;
                border-radius: $font-l;
                cursor: pointer;
                border: 2px solid transparent;

                .draft-type-logo {
                    height: 4rem;
                    width: 4rem;
                    border: 1px solid $form-text-sub-b;
                    border-radius: 10rem;
                    padding: 0.5rem;
                    margin-right: 1rem;
                    background-color: $form-bg-main;
                }

                .draft-type-logo.snake {
                    background-image: url("../images//icons//snake.png");
                    background-position: center;
                    background-size: 70%;
                    background-repeat: no-repeat;
                }

                .draft-type-logo.linear {
                    background-image: url("../images//icons//top-right.png");
                    background-position: center;
                    background-size: 70%;
                    background-repeat: no-repeat;
                }

                .draft-type-details {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;

                    p.draft-type-title {
                        font-size: $font-m;
                        font-family: poppins, sans-serif;
                        font-weight: 700;
                        color: $form-text-main;
                        padding-bottom: 0.5rem;
                    }

                    p.draft-type-info {
                        font-size: $font-s;
                        font-family: poppins, sans-serif;
                        font-weight: 400;
                        color: $form-text-sub-a;
                    }
                }


            }

            .new-league-draft-type.selected {
                border: 2px solid $form-text-hl;
                background-color: $form-bg-select;
            }

            
        }
    }

    .new-league-btn {
        width: 420px;
        font-size: $font-m;
        // background-color: $form-text-hl;
        background: linear-gradient(315deg,#00b7b3,#4ce2a7);
        border-radius: 10rem;
        padding: $font-m;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }

    .new-league-btn:hover {
        background: linear-gradient(100deg,#00b7b3,#4ce2a7);
    }


}




